
<style lang='less' scoped>
  @import '../../../assets/less/main.less';
  .DailyInfo-layout {
    width: 500px;
    padding: 30px 0 0 40px;
    font-size: @text;
    color: @textColor;
  }
  .cell-box {
    min-height: 60px;
    .displayCenter(@justify-content: flex-start);
    .title {
      width: 90px;
    }
    .form {
      width: 250px;
      #ToolTip {
        width: 250px;
      }
    }
    .text-optional {
      font-size: @smallText;
      color: @supportTextColor;
    }
  }
</style>

<template>
  <div class="DailyInfo-layout">
    <div class="cell-box">
      <div class="title"></div>
      <div class="form">                                                      
                                                                        <!-- :options="disableDate"  -->
        <DatePicker type="date" placeholder="" @on-change="changeDate" size="large" style="width: 250px"></DatePicker>
      </div>
    </div>
    <div class="cell-box">
      <div class="title"></div>
      <div class="form">
        <Input v-model="dailyInfo.sentence" size="large" placeholder="" />
      </div>
    </div>
    <div class="cell-box">
      <div class="title"></div>
      <div class="form">
        <Input v-model="dailyInfo.english" size="large" placeholder="" />
      </div>
    </div>
    <div class="cell-box">
      <div class="title"></div>
      <div class="form">
        <Tooltip id="ToolTip" content=": n." placement="right">
          <Input v-model="noun" size="large" placeholder="" />
        </Tooltip>
      </div>
      <span class="text-optional">()</span>
    </div>
    <div class="cell-box">
      <div class="title"></div>
      <div class="form">
        <Tooltip id="ToolTip" content=": v. fence" placement="right">
          <Input v-model="verb" size="large" placeholder="" />
        </Tooltip>
      </div>
      <span class="text-optional">()</span>
    </div>
    <div class="cell-box">
      <div class="title"></div>
      <div class="form">
        <Tooltip id="ToolTip" content=": adj. " placement="right">
          <Input v-model="adj" size="large" placeholder="" />
        </Tooltip>
      </div>
      <span class="text-optional">()</span>
    </div>
    <div class="cell-box">
      <div class="title"></div>
      <div class="form">
        <Tooltip id="ToolTip" content="////" placement="right">
          <Input v-model="other" size="large" placeholder="" />
        </Tooltip>
      </div>
      <span class="text-optional">()</span>
    </div>
    <div class="cell-box" style="marin:0px;">
      <div class="title"></div>
      <div class="form">
        <Cropper :width="width" :height="height" imgtype="Base" v-on:datafromCropper="datafromCropper"></Cropper>
      </div>
    </div>
    <div class="cell-box" style="marin:0px;">
      <div class="title"></div>
      <div class="form">
        <Button type="success" @click="clickSubmit" :loading="buttonLoading" size="large" long> </Button>
      </div>
    </div>
  </div>
</template>

<script>
  import GLOBAL from './../../../assets/GLOBAL.vue'
  import Cropper from './../../../components/Cropper.vue'
  export default {
    name: "DailyInfoAdd",
    data() {
      return {
        // 
        width: 340,
        height: 200,
        // 
        noun: '',
        // 
        verb: '',
        // 
        adj: '',
        // 
        other: '',
        // 
        dailyInfo: {
          date: '',
          sentence: '',
          english: '',
          chinese: '',
          picture: ''
        },
        // 
        buttonLoading: false,
        // 
        // disableDate: {
        //   disabledDate(date) {
        //     return date && date.valueOf() < Date.now() - 86400000;
        //   }
        // },
      }
    },
    components: {
      Cropper
    },
    computed: {
      CHINESE() {
        const chinese = this.noun + '}' + this.verb + '}' + this.adj + '}' + this.other +'}'
        return chinese
      }
    },
    methods: {
      changeDate(date) {
        const DATE = date
        this.dailyInfo.date = DATE
      },
      datafromCropper(data) {
        const _CropperInfo = data
        this.dailyInfo.picture = _CropperInfo
      },
      // 
      clickSubmit() {
        this.dailyInfo.chinese = this.CHINESE
        const {
          date,
          sentence,
          english,
          chinese,
          picture
        } = this.dailyInfo
        this.$http.post(GLOBAL.URL + 'daily/insert', {
          date: date,
          sentence: sentence,
          english: english,
          chinese: chinese,
          picture: picture
        }).then(res => {
          if (res.data.msg === '') {
            this.$Message.success({
              content:'',
              duration:5
            })
          }
        }, err => {})
      }
    },
    created() {
      // 
      this.$emit("changeBreadSon", 'Daily-info ')
    }
  }
</script>

